.story {
  p {
    span {
      width: 35px;
      height: 25px;
      background-color: #343434;
      position: absolute;
      left: 0;
      color: #fff;
      border-radius: 4px;
      text-align: center;
      transition: 0.3s linear;

      &::before {
        content: attr(data-valuenow);
        font-size: 13px;
      }

      &::after {
        content: "";
        border: 9px solid;
        border-color: #343434 transparent transparent transparent;
        position: absolute;
        bottom: -18px;
        left: 50%;
        transform: translateX(-50%);
      }
    }
  }

  .progress {
    border-radius: 3px;
    height: 5px;
    background-color: #ebebeb;
    margin-bottom: 20px;
    box-shadow: 0 1px 2px rgb(0 0 0 / 50%) inset;

    .progress-bar {
      width: 0%;
      position: relative;
      transition: 0.3s linear;
      background-color: $main-color  !important;
    }
  }
}