// Home Section Typography
.carousel-item {
  .carousel-caption {
    h1 {
      font-size: 50px;

      @include breakpoints(450px) {
        font-size: 30px;
      }
    }

    p {
      font-size: 14px;
    }
  }
}

// About Section Typography
.about {
  .box {
    p {
      font-size: 14px;
    }
  }
}

// Story Section Typography
.story {
  p {
    position: relative;
  }
}

// Team Section Typography
.team {
  .container {
    .box {
      .text {
        p {
          font-size: 14px;
        }
      }
    }
  }
}

// Price Section Typography
.price {
  .box {
    .price-head {
      h4 {
        sub {
          font-size: 15px;
        }
      }

      p {
        font-size: 13px;

        span {
          text-decoration: line-through;
        }
      }
    }

    .price-body {
      p {
        font-size: 14px;

        &:not(:last-child) {
          border-bottom: 1px solid #eee;
        }
      }
    }
  }
}