.contact {
  form {
    input {
      ::placeholder {
        font-weight: bold;
        text-transform: uppercase;
        color: $background;
      }
    }

    textarea {
      resize: vertical;
    }

    input,
    textarea {
      border-radius: 2px;
      font-size: $small-heading;
      border: none;
      outline: none;
      background-color: #f2f2f2;
    }

    textarea {
      min-height: 150px;
    }
  }

  .text {
    address {

      h6,
      span {
        color: $background;
      }

      p {
        font-size: 14px;
      }

      span {
        cursor: help;
        border-bottom: 1px dotted #777;
        font-weight: 600;
      }
    }
  }
}