.testimonials {
  position: relative;

  &::before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background-image: url(../imgs/backgrounds/bg-4.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    z-index: -1;
  }

  .carousel {
    min-height: 250px;

    .carousel-inner {
      .carousel-item {
        height: -webkit-fill-available;

        .carousel-item a {
          font-size: 14px;
          transition: 0.15s ease;

          &:hover {
            color: #aaa;
          }
        }
      }
    }
  }

}