.price {
  .box {
    border: 1px solid #0000001f;

    .price-head {
      background-color: #5f686f;
      clip-path: polygon(100% 0, 100% 87%, 49% 99%, 0 87%, 0 0);
      padding: 20px;

      button {
        background-color: #fff;
        transition: 0.3s ease-in;
        color: #5f686f;
        font-size: 15px;

        &:hover {
          color: #fff;
          background-color: $main-color;
        }
      }
    }
  }
}