.carousel-item {
  transition: transform 0.6s ease-in-out !important;
  height: 100vh;

  img {
    height: -webkit-fill-available;
    object-fit: cover;
  }

  .carousel-caption {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    height: fit-content;
    width: 100%;
  }
}