.portfolio {
  ul {
    li {
      background-color: #f6f7f7;
      cursor: pointer;
      font-weight: 400;
      font-size: 13px;
      position: relative;
      transition: 0.3s ease-in-out;

      &::before,
      &::after {
        position: absolute;
        left: 50%;
        opacity: 0;
        transition: 0.3s ease-in-out;
      }

      &::before {
        content: attr(data-products);
        width: 30px;
        height: 22px;
        background-color: $main-color;
        top: 50%;
        font-size: 13px;
        color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
        transform: translate(-50%, -50%);
        border-radius: 3px;
      }

      &::after {
        content: "";
        width: 10px;
        border: 6px solid;
        border-color: #91da32 rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
        top: 80%;
        transform: translateX(-50%);
      }

      &:hover {

        &::before,
        &::after {
          opacity: 1;
        }

        &::before {
          top: -50%;
        }

        &::after {
          top: -20%;
        }
      }

      &.active,
      &:hover {
        background-color: $main-color;
        color: #fff !important;
      }
    }
  }

  .row {
    .box {
      transition: 0.3s ease-in-out;
      position: relative;
      height: 100%;

      .content {
        opacity: 0;
        transition: 0.3s ease-in-out;
        top: 30%;
        transform: translateY(-50%);

        span {
          font-size: 14px;
        }

        .icons {
          svg {
            cursor: pointer;
          }
        }
      }

      &::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: $main-color;
        opacity: 0;
        transition: 0.3s ease-in-out;
      }

      &:hover {
        &::before {
          opacity: 0.8;
        }

        .content {
          opacity: 1;
          top: 50%;
          transform: translateY(-50%);
        }
      }
    }
  }

  .button {
    a {
      background-color: #f6f7f7;
      border: 1px solid $main-color;
      color: $main-color;
      transition: 0.25s ease-in-out;
      font-size: 12px;
      padding: 10px 15px;

      &:hover {
        background-color: $main-color;
        color: #fff;
      }
    }
  }
}