.navbar {
  background-color: $background;
  max-height: 320px;
  padding: 5px 0;
  position: fixed !important;
  width: 100%;
  background-color: transparent;
  .navbar-toggler {
    padding: 5px;
    border: none;
    &:focus {
      box-shadow: none;
    }
    svg {
      color: $main-color;
    }
  }
  .navbar-collapse {
    transition: $short-transition;
    @include breakpoints(991px) {
      background-color: $background;
    }
  }
  .nav-item {
    padding: 7px;
    @include breakpoints(991px) {
      &:not(:last-child) {
        border-bottom: 1px dotted #2a2a2a;
      }
    }
    .nav-link {
      transition: $short-transition;
      font-size: 15px;
      font-weight: 600;
      &:hover,
      &.active {
        color: $main-color !important;
      }
    }
  }
}
