.team {
  .container {
    .box {
      transition: 0.3s ease-in-out;
      .overlay {
        transition: 0.5s ease-in-out;
        position: absolute;
        left: 50%;
        top: 25%;
        opacity: 0;
        color: #fff;
        transform: translateX(-50%);
        svg {
          font-size: 22px;
          cursor: pointer;
        }
      }
      .text {
        position: absolute;
        padding: 15px 0px;
        bottom: -85px;
        background-color: rgba($color: #fff, $alpha: 0.97);
        max-width: 230px;
      }
      &::before {
        content: "";
        background-color: $main-color;
        width: 100%;
        height: 100%;
        opacity: 0;
        position: absolute;
        transition: 0.3s ease-in-out;
      }
      &:hover {
        &::before {
          opacity: 0.8;
        }
        .overlay {
          top: 50%;
          transform: translate(-50%, -50%);
          opacity: 1;
        }
      }
    }
  }
}
