// Render All ELements In HTML File
* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  outline: none;
}

body {
  font-family: "Open Sans", sans-serif !important;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none !important;
}

section,
footer {
  overflow: hidden;
  padding: 100px 0;
}

img {
  max-width: 100%;
}

.highlight {
  color: $main-color;
}

.main-heading {
  margin-bottom: 100px !important;

  p {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      height: 2px;
      background-color: $background;
      bottom: 0;
      width: 110px;
      margin-bottom: -20px;
    }
  }
}

::selection {
  background-color: $main-color;
  color: #fff;
}

.fade-in {
  transform: scale(0.7);
}

.fade-in,
.fade-out,
.fade-right,
.fade-left {
  opacity: 0;
  transition: $long-transition;
}

.fade-out {
  transform: scale(1.2);
}

.fade-right {
  transform: translateX(-40%);
}

.fade-left {
  transform: translateX(-40%);
}